@import 'variables';

.basket-voucher {
  margin: 1rem 0;

  &__button {
    background: $basket-voucher-button-bg-color;
    color: $basket-voucher-button-tx-color;
    border: $basket-voucher-button-border;
    padding: 1rem;
    text-align: left;

    & > i {
      padding: 0.25rem;
      margin-right: 12px;
      font-size: 17px;
    }
  }

  &__side-button {
    color: $pz-color-text-primary-light;
    background: $pz-color-primary-dark;
  }

  &__button-container {
    padding-top: 24px
  }

  &__form {
    display: none;

    & input {
      padding: 0.5rem 0.3rem;
      height: 3rem;
    }

    & button {
      height: 3rem;
      padding: 0 2rem;
      font-size: 0.9rem;
    }
  }
}
