.auth-page {
  &__title {
    text-align: center;
    padding: 0 30px;

    h1 {
      font-size: 24px;
    }

    @media (min-width: 768px) {
      padding: 0;
    }
  }

  .muted-text {
    font-size: 14px;
    color: #9d9d9d;
  }

  label.error {
    font-size: 14px;
    color: #d72b01;
  }

  .form-control,
  .form-control:focus {
    padding: 26px 10px;
    font-size: 14px;
    color: #000000;
    border: solid 1px #c9c9c9;
    border-radius: 0px;
    box-shadow: none;

    &.error {
      border-color: #d72b01;
    }
  }

  .link {
    font-size: 14px;
    color: #000000;
    text-decoration: underline !important;
  }

  @media (min-width: 768px) {
    max-width: 410px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .form-group--password {
    position: relative;
  }

  .password-display {
    position: absolute;
    right: 10px;
    top: 18px;
    font-size: 12px;
    text-decoration: underline;
    cursor: pointer;
  }
}


.modal-container {
  .modal-content {
    .close {
      text-align: right;
      margin-right: 33px;
      margin-top: 15px;
    }
  }

  .modal-header {
    justify-content: center;
    border-bottom: none;

    h5 {
      font-size: 35px;
    }
  }

  .modal-body {
    padding-left: 33px;
    padding-right: 33px;
    .modal__text {
      font-size: 14px;
      color: #9d9d9d;
    }

    input {
      width: 100%;
      padding: 13px;
      border: 1px solid #c9c9c9;
      outline: none;

      &::placeholder {
        color: #9d9d9d;
      }
    }

    .account__form-group--offset {
      margin-top: 15px;
    }

    .modal__text {
      a {
        font-size: 14px;
        color: #9d9d9d;
        margin-top: 15px;
        display: block;
      }
    }
  }
}