@import "get-widget/index";

.flatpage {
  margin: 40px auto;
  position: relative;

  &__sidebar {
    padding-right: 70px;

    &:after{
      content: "";
      right: 0;
      top: -40px;
      width: 1px;
      height: 100%;
      position: absolute;
      background-color: #dfdfdf;
    }
  }

  &__content {
    padding-left: 40px;

    @media only screen and (max-width: 768px) {
      padding-left: 0;
      margin-top: 40px;
    }
  }

  &__description{
    color: #c9c9c9;
    font-size: 16px;
    margin-top: 15px;
  }

  &__title {
    font-size: 24px;
    margin-bottom: 16px;
    line-height: normal;
  }

  &__menu {
    margin: 0;
    padding: 0;

    &-link {
      margin-top: 12px;

      a {
        color: #666666;
        font-size: 13px;
        font-weight: 300;
      }

      &.active {
        a {
          color: #000000;
          font-weight: 700;
          text-decoration: underline !important;
        }
      }
    }
  }

}