@import '~jquery-ui.1.11.1/themes/base/datepicker.css';

.profile-form {
	padding-bottom: 20px;
}

.account__container {
	.account__content__description {
		color: #9d9d9d;
	}
	#changePasswordFormWrapper {
		.input-wrapper {
			margin-bottom: 0;
		}
		.password-input-wrapper {
			margin-bottom: 20px;
		}
		.password-description {
			font-size: 14px;
			color: #9d9d9d;
		}
		.p-error-msg {
			font-size: 12px;
			text-align: right;
			display: block;
			color: #cc5965;
		}
		label{
			&.error{
				font-size: 12px;
				text-align: right;
				display: block;
			}	
		}
	}
	#ChangeEmailFormWrapper {
		.input-wrapper {
			margin-bottom: 0;
		}
		.email-input-wrapper {
			margin-bottom: 20px;
		}	
		.p-error-msg {
			font-size: 12px;
			text-align: right;
			display: block;
			color: #cc5965;
		}
		label{
			&.error{
				font-size: 12px;
				text-align: right;
				display: block;
			}	
		}
	}
}

#profileFormWrapper {
	margin-top: 32px;

	.input-label-wrapper {
		position: relative;

		&:nth-child(1) {
			margin-right: 11px;
		}
		&:nth-child(-n+2) {
			width: calc(50% - 8px);
			display: inline-block;
			float: left;
		}
		.label_ {
			position: absolute;
			top: 5px;
			left: 17px;
			font-size: 10px;
			color: #9d9d9d;
		}
		input,
		textarea {
			&:focus {
				&::placeholder {
					color: transparent;
				}
			}
		}
	}

	.requirement-info-text {
		font-size: 14px;
		margin: 24px 0 32px 0;
		color: #9d9d9d;
	}

	label {
		&.error {
			font-size: 12px;
			text-align: right;
			display: block;
		}
	}
}

#ui-datepicker-div {
	background-color: #fff;
	border: 1px solid #9d9d9d;
	padding: 0 20px;
	color: #9d9d9d;

	a {
		color: #000;
	}
}

.contracts-link {
	font-weight: bold;
	color: #000;
	text-decoration: underline !important;
}