.footer {
  background: #ffffff;
  padding: 145px 25px 25px 25px;
  border-top: 1px solid #e1e1e1;

  @media only screen and (max-width: 991px){
    padding: 60px 15px 30px 15px;
  }

  @media only screen and (max-width: 767px){
    padding-bottom: 116px;
  }

  &__logo {
    font-size: 70px;
    @media only screen and (max-width: 991px) {
      font-size: 45px;
    }
  }

  nav {
    padding-left: 110px;

    @media only screen and (max-width: 991px) {
      padding-left: 0;
      padding-top: 30px;
    }
  }

  .subscribe {
    &__box {
      padding-left: 60px;

      form {
        input {
          &[type="email"] {
            width: 295px;
            height: 44px;
            border: none;
            padding: 0 10px;
            background-color: #ffffff;
            border-bottom: 1px solid #000000;

            &::placeholder {
              font-size: 14px;
              color: #818181;
            }
          }
        }

        button {
          width: 94px;
          border: none;
          height: 44px;
          color: #ffffff;
          position: relative;
          background-color: #000000;
        }
      }

      @media only screen and (max-width: 991px) {
        padding-left: 0;
      }
    }

    &__text {
      color: #666666;
      padding: 25px 0;
      font-size: 14px;
      line-height: 1.57;
      letter-spacing: normal;
    }
  }

  &__menu {
    &__title {
      display: flex;
      color: #000000;
      font-size: 14px;
      align-items: center;
      margin-bottom: 24px;
      font-family: "Plain Bold";
      justify-content: space-between;

      .icon-chevron_top {
        display: none;
      }

      &.active {
        .icon-chevron_top {
          display: block;
        }

        .icon-chevron_down {
          display: none;
        }
      }

      @media only screen and (max-width: 991px) {
        + .footer__menu_subtitle {
          display: none;
        }

        &.active {
          margin-bottom: 0;
          + .footer__menu_subtitle {
            display: block;
          }
        }
      }
    }

    &_subtitle {
      li {
        display: block;
        color: #666666;
        font-size: 14px;
        font-weight: 500;
        text-align: left;
        line-height: 1.57;
        padding-bottom: 5px;
        font-family: "Plain Regular";

        a {
          display: block;
          color: #666666;
          font-size: 14px;
          font-weight: 300;
          line-height: 1.57;
          font-family: "Plain Regular";
        }
      }

      &.support__menu {
        li {
          padding-bottom: 15px;
        }
      }
    }
  }

  .social_box {
    a {
      color: #000000;
      font-size: 24px;
      padding-right: 15px;
    }
  }

  &__lang {
    span {
      color: #666666;
      font-size: 14px;
      padding-right: 25px;
    }

    a {
      color: #666666;
      font-size: 14px;
      line-height: 1.57;
      padding-right: 15px;
      text-decoration: underline !important;

      &.active {
        color: #000000;
      }
    }
  }

  &__copyright {
    color: #666666;
    font-size: 14px;
  }

}

.footer ul {
  padding-inline-start: 0;
}