.smart-shop {
  background-color: #f8f8f9;

  .big-title {
    width: 60%;
    margin: 0 auto;
    color: #a65341;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
  }

  .smart-box {
    &-image, &-content {
      flex-grow: 1;
      flex-basis: 0;
      max-width: 100%;
      position: relative;
    }

    &-image {
      img{
        width: 100%;
      }
    }

    &-content {
      margin-left: 20px;
      background-size: 27px 3px;
      background-position: bottom;
      background-repeat: repeat-x;
      background-image: linear-gradient(to right, #a65341 60%, rgba(255, 255, 255, 0) 0%);

      h2 {
        color: #a65341;
        font-size: 32px;
        margin-left: 15px;
      }

      p {
        margin: 10px 0 0 15px;
      }

      a {
        color: #ffffff;
        font-size: 16px;
        min-width: 160px;
        padding: 5px 15px;
        display: inline-block;
        margin: 150px 0 25px 15px;
        background-color: #a65341;

        i {
          font-weight: 600;
          padding-right: 15px;
          vertical-align: text-bottom;
        }
      }
    }

    &:not(.second-box) {
      .smart-box-image {
        text-align: right;
      }
    }

    &.first-box {
      margin-top: 45px;
    }

    &.second-box {
      margin-top: -100px;
      .smart-box-content {
        text-align: right;
        margin: 0 20px 0 0;
        background-image: linear-gradient(to right, #0f715d 60%, rgba(255, 255, 255, 0) 0%);


        h2 {
          color: #0f715d;
          margin-right: 15px;
        }

        p{
          margin: 10px 15px 0 0;
        }

        a {
          text-align: left;
          margin-right: 15px;
          background-color: #0f715d;
        }
      }
    }

    &.third-box{
      margin-top: -100px;
    }
  }
}