.orders__head {
	margin-bottom: 64px;

	@media (max-width: 991px){ 
		margin-bottom: 16px;
	}
}

.order__list--head {
	padding: 8px 0;
  border-bottom: solid 1px #dfdfdf;

  @media (max-width: 991px){
  	display: none !important;
  }
}

.order__list-empty {
	font-size: 14px;

	@media (min-width: 991px){
		padding: 13px 15px;
	}
}

.orders__list__column {
	width: 20%;
	padding: 0 15px;
	text-align: left;
	font-size: 14px;
	position: relative;

	@media (max-width: 991px){
		width: 100%;
  	text-align: right;
  	padding: 0;

  	&:before {
	  	content: attr(data-label);
	  	position: absolute;
	    left: 0;
	    color: #666666;
	  }
  }

	&--link {
		color: #000 !important;

		&:hover{ 
			text-decoration: underline !important;
		}
	}
}



.order__list--head .orders__list__column {
	color: #666666;	
}

.order__list__item {
	padding: 24px 0 14px;
	border-bottom: 1px solid #dfdfdf;

	&:last-child {
		border-bottom: 0 none;
	}
}

.order__list__product-image {
	width: 110px;
	height: 131px;
	margin-bottom: 10px;
	margin-right: 10px;
}

.order__list__image-wrapper {
	padding: 20px 15px 0;

	&-easy_return {
		position: relative;

		.order-detail-easy_return-wrapper {
			position: absolute;
	    bottom: 15px;
	    left: 5px;
	    margin: 0;
	    padding: 6px 0 3px 0;
	    width: calc(100% - 20px);
	    background-color: #fff;
		}

		.order-detail-easy_return-text {
			font-size: 8px;
		}

	}
}