@import './variables.scss';
@import '../components/product/slider/index.scss';
@import '../components/product/content/index.scss';

/* Slider Start */
$product-thumbnail-active-indicator: #bae4f6;
$product-thumbnail-active-border-width: 2px;

%thumbnail-after {
  content: '';
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  border-color: $product-slider-thumbnail-border-color;
  border-bottom-color: $product-thumbnail-active-indicator;
  border-style: $product-slider-thumbnail-border-type;
}

/* Slider End */

/* Product Start */
.product {
  &__page {
    .pz-breadcrumb {
      @media (max-width: 991px) {
        padding-left: 10px;
      }
    }
  }

  &__content {
    width: 379px;

    @media (max-width: 991px) {
      width: 100%;
    }
  }

  &__wrapper {
    padding-bottom: 47px;

    .product-item__discount-badge {
      @media (min-width: 992px) {
        left: 117px;
        z-index: 9999;
      }
    }
  }

  &__ghost-button {
    color: #212529;
    display: block;
    height: 52px;
    text-align: center;
    border: 1px solid;
    line-height: 50px;
    font-size: 0.70rem;
    align-items: center;
    margin-bottom: 0.2rem;
    margin-right: 5px;
    cursor: pointer;
    background-color: #ffffff;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      color: #212529;
    }
  }

  &__add-cart-button-wrapper {
    margin-bottom: 10px;
   
    @media (max-width: 761px) {
      display: flex;
      justify-content: space-between;
      position: fixed;
      width: 100%;
      left: 0;
      bottom: 0;
      z-index: 100;
      background-color: #fff;
      margin-bottom: 0;
      padding: 10px 15px;
    }

    .product__price, .product__price--currency {
      @media (max-width: 761px) {
        font-size: 18px;
      }
    }

    .product__retail__price {
      @media (max-width: 761px) {
        font-size: 14px;
        margin-left: 0;
      }

      .product__price--currency {
        @media (max-width: 761px) {
          font-size: 14px;
        }
      }
    }

  }

  &__title {
    font-family: 'Plain Regular';
  }

  &__retail__price {
    font-family: 'Financier Regular';
    text-decoration: line-through;
    line-height: 1;
    margin-left: 8px;
    padding-top: 6px;
  }

  &__shared-icons {
    padding: 25px 0 35px 0;
  }

  &__shared-icon {
    margin-right: 25px;
  }

  &__variants__wrapper:last-child {
    @media (max-width: 991px) {
      border-bottom: 1px solid #dfdfdf;
      margin-bottom: 0.5rem;
    } 
  }

  &__favourite.active {
    i:after {
      content: '\e901';
      font-family: 'icomoon';
    }
  }

  &__stock--error {
    width: 100px;
    text-align: right;
    margin-bottom: 10px;
    font-size: 12px;
    color: #818181;
  }

  &__offer--text {
    margin-bottom: 10px;
    font-size: 12px;
    color: #9d9d9d;
  }

  &__instructions--image {
    height: 18px;
  }

  &__basket-offer {
    border: 1.5px solid #f27a1a;
    border-radius: 50px;
    color: #f27a1a;
    font-size: 13px;
    padding: 5px 10px;
    display: inline-block;
  }
}

.zooContainerClass {
  .zoomContainer {
    z-index: -1 !important;
  }
}

.zoomContainer{
  .zoomWindowContainer {
    pointer-events: none;
  }
}

/* Product End */


/* Accordion Start */
.accordion-item{
  margin-bottom: 25px;

  &__title {
    border-bottom: solid 1px #dfdfdf;
  }

  &__content {
    color: #666666;
    font-size: 0.88rem;
    padding-top: 5px;

    &--box {
      padding-left: 18px;

      p {
        list-style: disc;
        display: list-item;
        margin-bottom: 2px;
      }
    }
  }
}
/* Accordion End */


/* Product Widget Start */
.product__widget {
  @media (min-width: 991px){

    .lSSlideWrapper {
      height: 350px;
      .js-reco-slider.lSSlide {
        height: 350px !important;
      }
    }
  }
    .product__widget__item {
      a {
        color: #000;
        display: block;
      }
      .product-item-slider-retail-price {
        margin-right: 0 !important;
      }
    }
  padding: 56px 0;
  @media (max-width: 991px) {
    padding: 32px 0;    
  }

  h3 {
    @media (max-width: 991px) {
      font-size: 24px;
      margin-bottom: 25px;
    }
  }

  &:nth-child(even){
    background-color: #f7f7f7;
  }

  &__item__img {
    width: 200px;
    margin-bottom: 15px;
  }

  &__item__name {
    width: 200px;
    margin: auto;
    margin-bottom: 15px;

    @media (max-width: 991px) {
      font-size: 13px;
    }
  }

  &__item__price {
    font-family: 'Financier Regular';
    font-size: 18px;
    font-weight: bold;

    @media (max-width: 991px) {
      font-size: 16px;
    } 
  }
}
/* Product Widget End */