.search {
  &__box {
    cursor: pointer;

    i {
      font-size: 22px;
    }

    &.active {
      .search-form {
        input {
          opacity: 1;
          width: 205px;
          padding: 0 10px 5px 30px;
          visibility: visible;
          transition: all 0.3s linear;

          @media only screen and (max-width: 768px) {
              width: 100%;
              border-radius:0;
          }
        }
        button{
          visibility: visible;
        }
      }

      .search__box__icon {
        display: none;
      }
    }

    .search-form {
      position: relative;

      input {
        width: 0;
        opacity: 0;
        z-index: 10;
        border: none;
        outline: none;
        box-shadow: none;
        visibility: hidden;
        position: relative;
        background-color: transparent;
        border-bottom: 1px solid #000000;
      }
    }

    button {
      top: -3px;
      z-index: 0;
      height:30px;
      left: -10px;
      cursor: auto;
      border: none;
      outline: none;
      color: #000000;
      box-shadow: none;
      position: absolute;
      visibility: hidden;
      background-color: transparent;
    }
  }
}

.autocomplete {
  &__dropdown {
    min-width: 500px;
  }

  &__img {
    width: 60px;
  }
}
