.navigation__menu__item {
  position: relative;
  padding-bottom: 20px;

  a {
    color: #000000;
    font-size: 14px;
    position: relative;
  }
  .red {
    color: #d72b01 !important;
  }
}

.navigation__menu__item{
  &:hover {
    .navigation__menu__main{
      a {
        font-weight: bold;
        padding-bottom: 22px;
        &:after{
          content: "";
          left:0;
          bottom: 0;
          width: 100%;
          height: 3px;
          position: absolute;
          background-color: #000000;
        }
      }
    }
    .navigation__submenu {
      opacity: 1;
      display: block;
      visibility: visible;
      -o-transition:all 0.3s linear;
      -ms-transition:all 0.3s linear;
      -moz-transition:all 0.3s linear;
      -webkit-transition:all 0.3s linear;

      .navigation__link__wrapper{
        display: flex;
        flex-wrap: wrap;
        align-self: flex-start;
      }

      &__link{
        display: flex;
        flex: 1 1 100%;
      }
      .two-column {
        flex: 1 1 50%;
      }
    }
  }
}

.navigation__submenu {
  z-index: 3;
  opacity: 0;
  width: 100%;
  left: -30px;
  padding: 30px;
  margin-top: 20px;
  position: absolute;
  visibility: hidden;
  line-height: 2.14;
  min-width:630px;
  background-color: #ffffff;
  border: 1px solid #c9c9c9;

  .navigation__action__link{
    font-size: 19px;
    font-family: "Financier Medium";
    text-decoration:underline !important;
    i{
      bottom: 3px;
      right: -20px;
      font-size: 12px;
      font-weight: bold;
      position: absolute;
      &:last-child{
        right: -24px;
      }
    }
  }
}
