@import '../components/address-form/index.scss';

.checkout {
  &__summary-product-name {
    height: 22px;
    overflow: hidden;
    max-width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__basket-item-price-wrapper > div {
    line-height: 1;

    &.basket-item__retail-price {
      margin-top: 3px;
    }
  }

  .basket-item__price {
    font-size: 16px;
  }

  &__summary-products-list {
    position: relative;
    padding-bottom: 30px;

    @media (max-width: 991){
      padding-bottom: 45px;
    }
  }

  &__summary-product-item-bottom {
    position: absolute;
    width: calc(100% - 0.5rem);
    left: 0.5rem;
    bottom: 0;
  }

  &__header {
    font-size: 60px;
    text-align: center;
    border-bottom: 1px solid #dee2e6;
  }
  &__tab {
    color: #ababab;
    font-size: 16px;

    @media (min-width: 576px) {
      font-size: 13px;
    }

    &.active {
      color: #212529;
    }
  }

  &__card-img {
    position: absolute;
    bottom: 0px;
    right: 0px;
  }

  &__steps {
    @media (max-width: 575px) {
      justify-content: space-around;
    }
  }

  &__steps-icon {
    color: #ababab;
  }

  &__shipping,
  &__installments {
    @media (min-width: 576px) {
      border-left: 1px solid #dee2e6;
    }
  }

  &__installments {
    &-title {
      @media (max-width: 575px) {
        display: none;
      }
    }

    &-content {
      @media (max-width: 575px) {
        border-top: 1px solid #dee2e6;
      }
    }
  }

  &__summary {
    &-title {
      font-size: 23px;
      line-height: 1.3;
    }

    &-gift-button{
      color: #000;
      font-size: .8rem;

      span{
        margin-left: 5px;
        margin-right: 5px;
      }
    }
  }

  &__address-box {
    & > div {
      margin: 0.5rem;
      padding: 0.5rem;
      border: 1px solid #dee2e6 !important;
      font-size: 14px;
      
      @media (max-width: 575px) {
        height: auto !important;
      }
    }

    .icon-plus_circle{
      font-size: 19px;
      margin-right: 8px;
      padding-top: 3px;
    }
    
    .address-box__content {
      word-break: break-all;
      overflow: hidden;
    }

    .adress-box__title {
      overflow: hidden;
    }
  }

  &__payment-tabs{
    z-index: 1;
  }

  &__payment-tab {
    background-color: rgba(215, 215, 200, 0.1);
    color: #9b9b9b;
    border: 1px solid #e5e5e5;
    font-size: 24px;

    &.active {
      background-color: #ffffff;
      color: #000000;
      border: 1px solid #dee2e6;
      border-bottom: 1px solid #ffffff;
    }
  }

  &__payment-contents {
    margin-top: -1px;
  }

  &__success-items {
    width: 300px;

    @media (min-width: 768px) {
      width: 600px;
    }
  }

  &__success-item {
    @media (min-width: 768px) {
      &:nth-child(odd) {
        padding-right: 0.5rem;
        border-right: 1px solid #979797;
      }

      &:nth-child(even) {
        padding-left: 0.5rem;
      }
    }
  }

  &__success-image {
    width: 58px;
    height: 88px;
  }

  &__success-title {
    font-size: 30px;
    text-align: center;

    @media (min-width: 576px) {
      font-size: 36px;
    }
  }

  &__success-muted {
    color: #818181;
    font-size: 12px;
  }

  &__success-price {
    line-height: 1;
  }

  &__success-retail {
    color: #818181;
    line-height: 1;
    text-decoration: line-through;
  }

  &__success-button {
    width: unset;
    padding: 0px 30px;
  }

  input[type=checkbox],
  input[type=radio] {
    font-size: 1em;
  }

  .border,
  .border-bottom {
    border-color: #dfdfdf;
  }

  .basket-item {
    border-bottom: 1px solid #dfdfdf;
  }

  .basket-item__image {
    width: 58px !important;
    height: 88px !important;

    @media (max-width: 991px){
      width: 94px !important;
      height: 143px !important;
    }
  }

  &__date-picker {
    flex: 1;
    height: 38px;
    
    .select {
      border-color: #ced4da;
    }
  }

  div.error {
    color: #d72b01;
  }

  .form-control.has-errors {
    border-color: #d72b01;
  }

  label.has-errors {
    input:before {
      color: #d72b01;
    }

    .color-secondary {
      color: #d72b01 !important;
    }
  }

  &-auth-page {
    max-width: unset !important;

    .nav-tabs {
      border-bottom: 1px solid #c9c9c9;
    }

    .nav-link {
      border-top-left-radius: 0px;
      border-top-right-radius: 0px;
      border: 1px solid #c9c9c9;
      color: #495057;
      background-color: #f5f5f5;

      &.active {
        border-color: #c9c9c9 #c9c9c9 #fff;
        background-color: #fff;
      }
    }
  }

  .icon-siparis_onay {
    font-size: 150px;
  }

  .cvc-desc {
    color: #3f4737;
    font-size: 14px;
    position: relative;

    img{
      left:0;
      top: 25px;
      z-index: 1;
      opacity: 0;
      transition: .2s;
      position: absolute;
      visibility: hidden;
    }

    &:hover{
      img{
        opacity: 1;
        visibility: visible;
      }
    }

    @media (max-width: 768px) {
      position: initial;
      img{
        top: 95px;
        width: 300px;
        left: -200px;
      }
    }
  }

  label.error {
    margin-left: 0;
    font-size: 14px;
  }

  .cvc-group .error {
    order: 3;
  }
}