.product-image-wrapper {
	padding-bottom: 149.567%;

	.product-item__img {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.product-item-favourite-elem {
	position: absolute;
  bottom: 0;
  right: 0;
  width: 44px;
  height: 44px;

  &:after {
  	font-family: 'icomoon';
  	content: "\e902";
  	color: #212529;
  	position: absolute;
    bottom: 16px;
    right: 17px;
  }

  &.active:after {
  	content: '\e901';
  }
}