@import 'variables';

$quantity-color: #666666;

.basket-item {
  border-bottom: 1px solid $pz-color-text-disabled-dark;
  padding: 1rem 0;

  .product-info {
    padding-left: 15px;
  }

  &__remove {
    &>a {
      color: $basket-item-remove;
      background: white;
    }
  }

  &__quantity {
    border: 1px solid #dfdfdf;
    font-size: 12px;
    height: 36px;
    width: 74px;
    margin-bottom: 10px;

    &>button {
      display: block;
      border: none;
      width: 2rem;
      min-width: 1.5rem;
      padding: 0;
      background-color: transparent;
      color: #666666;
    }

    &__input {
      width: 1rem;
      height: 2rem;
      border: none;
      padding: 0;
      text-align: center;
      color: #666666;
    }

    &>button,
    &__input {
      &:focus {
        outline: 0;
      }
    }
  }

  @media (min-width: 767px) {
    &-product-image {
      max-width: 100px;
    }
  }

  &__image {
    width: 94px !important;
    height: 143px !important;
  }

  &__product-name {
    padding:0 30px 0 0;

    &>a {
      color: $basket-item-product-name;
      font-size: 14px;
      margin-bottom: 10px;
      display: block;
    }
  }

  &__product-description {
    color: $pz-color-text-secondary-dark;
    font-size: 0.9rem;
  }

  &__product-attr {
    color: #818181;
    font-size: 12px;
  }

  @media (min-width: 767px) {

    .price-container {
      margin: auto;
      margin-right: 0;
      padding-left: 15px;
    }
  }

  &__retail-price,
  &__price {
    font-family: 'Financier Regular';
  }

  &__retail-price {
    color: $basket-item-retail-price;
    text-decoration: line-through;
    color: #818181;
    font-size: 14px;
  }

  &__price-currency {
    font-size: 0.95rem;
  }

  &__price {
    font-weight: bold;
    color: $basket-item-price;
    font-size: 1.2rem;
  }
}