.order-cancellation-wrapper {
  max-width: 684px;
}

.order-cancellation-list-title {
  font-size: 14px;
  padding-bottom: 7px;
  border-bottom: solid 1px #dfdfdf;
}

.order-cancellation-action {
  margin-top: 18px;
  
  form {
    max-width: 100% !important;
  }

  .input-wrapper {
    margin-bottom: 0 !important;
  }
}

.order-cancellation-checkbox {
  width: 16px;

  @media (max-width: 991px){
    display: inline-block;
  }
}

.order-cancellation-button {
  width: 150px;
  height: 36px;
  line-height: 34px;
  font-size: 14px;

  @media (max-width: 991px){
    width: 100%;
  }
}

.order-cancellation-agreement {
  .contracts-link {
    font-size: 14px !important;
    color: #9d9d9d !important;
  }
}

.order-cancellation-product {
  padding: 10px 0;
  border-bottom: solid 1px #dfdfdf;
}


.order-cancellation-image-wrapper {
  margin-left: 18px;
}
  
.order-cancellation-select {
  width: 259px;

  @media (max-width: 991px){
    margin-top: 20px;
    width: 100%;
  }

  .select2-selection--single {
    height: 36px !important;
  }

  .select2-selection__rendered, 
  .select2-selection__placeholder {
    line-height: 34px !important;
  }
}

.order-cancellation-product-info {
  padding-left: 15px;
  width: calc(100% - 389px);

  @media (max-width: 991px){
    width: calc(100% - 140px);
  }
}

.order-cancellation-info {
  padding: 5% 0 5% 5%;
  border-bottom: solid 1px #dfdfdf;
  margin-bottom: 18px;
  
  input {
    border: solid 1px #c9c9c9;
    background-color: #ffffff;
    padding: 8px 5px 9px 16px;
  }

  p {
    color: #818181;
    font-family: Plain;
  }
  
  .info-box { 
    
    .form-row {
      margin-bottom: 8px;
      flex-wrap: nowrap;

      input {
        border: solid 1px #c9c9c9;
        background-color: #ffffff;
        padding: 8px 5px 9px 16px;
        
        @media (max-width: 991px){
          width: 100%;
        }
        
      }
      .input-iban {
        width: 100%;
      }
    }
  }
}