@import './variables.scss';

.product__variant--image {
  width: $product-variant-image-width;
  height: $product-variant-image-height;

  @media (max-width: 991px){
    width: 40px;
    height: 59px;
  }
}

.product__variant {
  position: relative;
  padding-bottom: 10px;
  border-bottom: 2px solid transparent;
  margin-right: 20px;

  &:after {
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
    width: 100%;
  }

  &.disabled {
    opacity: 0.3;
  }

  &.selected:after {
    background-color: #000;
  }

  &[data-attribute-name="Drop"]{
    pointer-events: none;
  }

  &--title {
    font-size: 12px;
    margin-bottom: 10px;
  }

  &--error {
    font-size: 12px;
    
    &.red {
      color: #d72b01;
    }
  }
}