@import './variables.scss';

@import '../node_modules/lightslider/dist/css/lightslider.min.css';
@import '../node_modules/lightgallery/src/sass/lightgallery.scss';

.product-slider {
  width: 100%;

  @include make-container-max-widths();

  @media (min-width: 992px) {
    max-width: $product-slider-width;
    flex-shrink: 0;
  }
}

.product-slider__wrapper {
  position: relative;
}

.product-images__wrapper {
  height: auto !important;
  padding-left: 0px;

  @media (min-width: 992px) {
    margin-left: 117px;
  }
}

.product-thumbnail__wrapper {
  padding-left: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.product-slider__image {
  @include img-fluid();
  width: $product-slider-width;
  height: $product-slider-height;
  margin-bottom: 30px;

  @media (max-width: 991px) {
    width: 100%;
    height: auto;
  }
}

.product-thumbnail__item {
  img {
    width: $product-thumbnail-width;
    height: $product-thumbnail-height;
    opacity: 0.3;
    margin-bottom: 17px;
  }

  &.active img {
    opacity: 1;
  }
}