.order-detail-title {
  font-family: 'Financier Regular';
  font-size: 36px;
  line-height: 1;

  @media (max-width: 991px){ 
    font-size: 24px;
  }
}

.order-detail-shipping {
  color: #9d9d9d;
  font-size: 16px;
  margin-bottom: 64px;

  @media (max-width: 991px){ 
    margin-bottom: 43px;
  }
}

.order-detail-wrapper {
  width: 100%;
  max-width: 684px;
}

.order-detail-list-head {
  padding: 12px 0;
  border-bottom: solid 1px #dfdfdf;

  form {
    width: 100%;
  }

  .order-detail-select-wrapper {
    width: calc(100% - 122px);
  }

  button {
    height: 36px;
    width: 122px;
    line-height: 34px;
    font-size: 14px;

    @media (max-width: 991px){ 
      width: 141px;
    }
  }

  .select2-selection--single {
    height: 36px !important;
  }

  .select2-selection__rendered, .select2-selection__placeholder {
    line-height: 34px !important;
  }
}

.order-detail-list-head-label {
  font-size: 14px;

  &:last-child {
    color: #666666;
  }
}

.oder-detail-product-item {
  padding: 10px 0;
  border-bottom: solid 1px #dfdfdf;
}

.order-detail-product-info {
  padding-left: 15px;
  width: calc(50% - 94px);

  @media (max-width: 991px){
    float: left;
    width: calc(100% - 94px);
    margin-bottom: 20px;
  }
}

.order-detail-product-amount {
  @media (max-width: 991px){
    float: left;
    width: calc(100% - 94px) !important;
    padding-left: 15px;
  }
}

.order-detail-image-wrapper {
  width: 94px;
  height: 143px;

  @media (max-width: 991px){
    float: left;
  }
}

.order-detail-product-name {
  margin-bottom: 5px;
  font-size: 14px;
}

.order-detail-product-attributes {
  color: #818181;
  font-size: 14px;
}

.order-detail-price {
  font-family: 'Financier Bold';

  @media (max-width: 991px){
    display: inline-block !important;
    margin-right: 10px;
  }
}

.order-detail-retail-price {
  color: #818181;
  text-decoration: line-through;
  font-size: 14px;

  @media (max-width: 991px){
    display: inline-block !important;
  }
}

.order-detail-product-quantity {
  color: #666666;
  font-size: 14px;
}

.order-detail-summary {
  padding: 22px 0;
  border-bottom: solid 1px #dfdfdf;
  margin-bottom: 15px;
}

.order-detail-summary-item {
  font-size: 14px;

  &:first-child {
    font-family: 'Plain Bold';
  }
}

.order-detail-summary-total {
  .order-detail-summary-item:last-child {
    font-size: 26px;
  }
}

.order-detail-list-head-labels {
  @media (max-width: 991px){ 
    width: 100% !important;
    margin-bottom: 20px;
  }
}


.order-detail-list-action-items {
  @media (max-width: 991px){ 
    width: 100% !important;
    padding-left: 0;
  }
}

.order-detail-summary > div,
.order-detail-summary-total > div {
  @media (max-width: 991px){ 
    width: 100% !important;
  }
}

.order-detail-easy_return {
  &-wrapper {
    border: solid 1px #0030af;
    padding: 6px 10px;
    text-align: center;
    margin: 10px 0;
  }

  &-text {
    font-size: 11px;
  }

  &-code {
    font-size: 11px;
    color: #ff6e00;
  }

  &-image {
    width: 48px;
    display: block;
    margin: 0 auto;
  }
}