.header {
  padding-top: 25px;
  background-color: #ffffff;
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.05);

  &__wrapper{
    height: 70px;
    @media only screen and (max-width: 768px) {
      height: auto;
    }
  }

  &__logo-box {
    padding-bottom: 20px;
    padding-right: 45px;
    margin-left: 45px;

    a {
      color: #000000;

      i {
        font-size: 70px;

        @media only screen and (max-width: 768px) {
          font-size: 45px;
        }
      }
    }
    @media only screen and (max-width: 768px) {
      padding-right: 10px ;
      padding-bottom: 15px;
    }
  }

  &__account {
    padding-bottom: 15px;
    @media only screen and (max-width: 768px) {
      padding-bottom: 15px;
    }
  }

  &__lang-box {
    a {
      color: #000000;
      font-size: 14px;
      text-decoration:underline !important;
    }
  }

  @media only screen and (max-width: 768px) {
    padding-top: 15px;
  }

  .menu__button{
    position: absolute;
    top: 25px;
    i{
      font-size: 18px;
    }
  }
}