.product {
  &-item {
    @media (max-width: 575px) {
      &:nth-child(odd) {
        padding-right: 5px;
        padding-left: 0px;
      }
  
      &:nth-child(even) {
        padding-left: 5px;
        padding-right: 0px;
      }
    }

    &__content {
      padding: 8px;
      flex: 1;
    }

    &__name {
      font-size: 13px;

      @media (min-width: 1024px) {
        font-size: 16px;
        line-height: normal;
        margin-bottom: 13px;
      }
    }

    &__category {
      font-size: 12px;
    }

    &__price {
      &--normal {
        font-size: 13px;
        
        @media (min-width: 1024px) {
          font-size: 16px;
        }
      }

      &--retail {
        color: #818181;
        text-decoration: line-through;
        font-size: 13px;
        margin-right: 25px;

        @media (min-width: 1024px) {
          font-size: 16px;
          margin-right: 20px;
        }
      }
    }

    &__badge {
      position: absolute;
      top: 0px;
      right: 0px;
      height: 60px;
    }
    
    &__discount-badge {
      position: absolute;
      top: 0px;
      left: 0px;
      height: 30px;
      padding-left: 10px;
      padding-right: 10px;
      background-color: #3561be;
      color: #ffffff;
    }
  }
}

.product-item-price-wrapper {
  height: 25px;
  margin-bottom: 10px;

  @media (min-width: 1024px) {
    margin-bottom: 13px;
  }
}

.product-item__basket-offer-wrapper {
  color: #fff;
  border-radius: 50px;
  background-color: #f27a1a;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  height: 28px;
  display: inline-flex;
  max-width: 100%;

  @media (min-width: 1024px) {
    align-items: center;
    justify-content: center;
  }
}

.product-item__basket-offers-label {
  font-size: 13px;
  padding: 5px 10px;

  @media (min-width: 1024px){
    font-size: 16px;
  }
}

.list {
  &__filters-btn {
    width: 54px;
    height: 54px;
  }

  &__categories {
    white-space: nowrap;
    overflow-x: auto;

    @media (max-width: 575px) {
      width: 100vw;
    }

    a {
      font-size: 12px;

      &.selected {
        background-color: #efefef;
      }
    }
  }

  &__description__text-wrapper {
    border-top: 1px solid #dfdfdf;
    padding: 22px 15px;
    
    .list__description__text {
        overflow: hidden;

      p:last-child {
        margin-bottom: 0;
      }
    }
    
    .read-more {
      align-items: center;
      color: #afafaf;
      font-weight: 700;
    }
  }

  &__facets--mobile {
    height: calc(100% - 97px);
    overflow: auto;
  }

  &__border {
    border: 1px solid #c9c9c9;
  }

  &__filters {
    .list__filters-choice-wrapper {
      margin-bottom: 29px;
    }

    &-checkbox {
      display: inline-block;
    }

    .color-secondary {
      margin-bottom: 13px;
    }

    .choice-label {
      padding-left: 20px;
      line-height: 1.4;
    }

    &--arrow {
      &:after {
        content: "\e910";
        display: inline-block;
        font-family: 'icomoon';
        font-size: 10px;
        margin-left: 8px;
      }
    }


    .dropdown.show {
      .nav-link {
        color: $pz-color-primary-dark !important;
      }

      .list__filters--arrow:after{ 
        content: "\e911";
      }
    }

    .nav-link {
      padding-top: 12px;
      padding-bottom: 12px;
    }

    &[data-type="selectboxes"]{
      .nav-link:first-child {
        padding-left: 0;
      }
    }

    &-menu {
      box-shadow: 0 2px 26px 0 rgba(0, 0, 0, 0.06);
      border: 0px;
      min-width: 290px;
      padding: 28px 49px 39px 37px;
      border-radius: 0px;
      text-align: center;

      button {
        width: 100%;
        line-height: 44px;
        padding: 0 20px;
      }

      &--bigger {
        div {
          display: grid;
          grid-template-columns: auto auto;
          grid-gap: 5px;
          margin-bottom: 10px;
  
          label {
            padding: 5px;
            margin-bottom: 0px;
            min-width: 100px;
          }
        }
      }
    }
  }
}

.list__sorter--wrapper {
  .dropdown-menu.show {
    margin-top: 12px;
  }
  
  & > div {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    line-height: 1 !important;
    margin-bottom: 0 !important;
    align-self: center !important;
  }
}  

.list__grid__icon {
  margin-right: 10px;
  color: #000 !important;

  &:last-child {
    margin-right: 0;
  }
}


.list__facets--mobile {
  @media (max-width: 992px){
    div[data-key=integration_beden1]{
      display: flex;
      flex-wrap: wrap;
      max-width: 349px;
      border-top: solid 1px #c9c9c9;
      border-left: solid 1px #c9c9c9;

      label {
        width: calc(100% / 6);
        height: 44px;
        margin-bottom: 0;
        max-width: 58px;

        input {
          display: none;

          & + span {
            display: inline-block;
            width: 100%;
            line-height: 44px;
            text-align: center;
            border-right: solid 1px #c9c9c9;
            border-bottom: solid 1px #c9c9c9;
            padding-left: 0 !important;
          }

          &:checked + span {
            background-color: #000;
            color: #fff;
          }
        }
      }
    }
  }  
}

.list__empty__product { 
  padding: 100px 0;
}

.product__list--row {
  flex-basis: 100%;
}

.list_banner__image__wrapper {
  position: relative;

  img {
    & + .list__banner__content .list__banner__title{
      font-size: 100px;
    }
  }

  .list__banner__content {
    position: absolute;
    top: 147px;
    left: 25px;

    @media (max-width: 992px){
      width: 100%;
      left: 0;
      text-align: center;
    }


    .list__categories {
      width: 100%;

      a.selected {
        background-color: transparent;
      }
    }
  }
}

.icon-uclu {
  &.active:before {
    content: "\e935";
  }
}

.icon-lu {
  &.active:before {
    content: "\e934";
  }
}

.sorter-mobile-label {
  padding-left: 15px;
}